import { isEmbed, isSocialWall, isTagShop, TagShopEditor } from "../../../actions/api";
import { getTextColor } from "../../../utils/Common";

export const StyleConstant = (wall) => {

       const { ThemeRule, ProductSetting, SnapUp, CustomTicker, ThemeStyle, CardStyle, Reviews } = wall
       const { CustomBanner } = wall

       const whiteColor = '#ffffff'
       const blackColor = '#000000'
       const inheritStyle = 'inherit'


       let Button, CatalogueTitle, Price, ProductTitle, Hotspot;

       if (typeof ProductSetting !== 'undefined' && ProductSetting) {
              ({ Button, CatalogueTitle, Price, ProductTitle, Hotspot } = ProductSetting);
       } else {
              CatalogueTitle = {
                     fontLink: "",
                     font: "",
                     variant: "",
                     size: '',
                     color: "",
                     text: ""
              };
              ProductTitle = {
                     fontLink: "",
                     font: "",
                     variant: "",
                     size: '',
                     color: ""
              };
              Price = {
                     fontLink: "",
                     font: "",
                     variant: "",
                     size: '',
                     color: ""
              };
              Button = {
                     fontLink: "",
                     font: "",
                     variant: "",
                     size: "",
                     background: "",
                     color: "",
                     text: "",
                     newTab: ""
              };
              Hotspot = {
                     type: "",
                     background: "",
                     color: "",
                     status: ""
              };
       }

       const getSuittableColor = (fgColor) => {
              var darkLightThemes = []
              var darkTheme = []
              if (isTagShop) {
                     darkLightThemes = [61, 16, 53]
                     darkTheme = [4, 55, 50]
              }

              if (isSocialWall) {
                     darkTheme = [74, 69, 71, 67, 65, 68]
              }

              const modeTheme = darkLightThemes.includes(wall?.ThemeInfo?.id) ? true : false
              const alwaysDark = darkTheme.includes(wall?.ThemeInfo?.id) ? true : false

              const bgColor = (modeTheme && CardStyle?.style.mode == 1) || alwaysDark ? blackColor : (modeTheme && CardStyle?.style.mode == 0) ? whiteColor : CardStyle?.style.background

              return getTextColor(fgColor, bgColor)
       }

       const getRoundedTheme = () => {
              var circleThemes = [55]
              return circleThemes.includes(wall?.ThemeInfo?.id) ? true : false
       }

       const splitFontSpec = (fontSpec) => {
              const parts = fontSpec ? fontSpec.split('-') : '';
              const weight = parts[0] ? parts[0] : '';
              const style = parts[1] ? parts[1] : '';
              return {
                     weight,
                     style,
              };
       }

       const getCssProperty = (isInherit, apiStyle, querySelector, css, cssProperty) => {
              if (isInherit) {
                     return querySelector
              } else {
                     return apiStyle != '' ? apiStyle : cssProperty
              }
       }

       const CtaData = CardStyle?.globalCTA && Object.keys(CardStyle?.globalCTA).length ? CardStyle?.globalCTA : {}
       const isInherit = CardStyle?.inheritStyles && !isSocialWall == 1 ? true : false

       const fontSize = CardStyle?.style.size;
       const css_font = CardStyle?.style.font;
       const authorColor = CardStyle?.author.color;
       const authorFont = CardStyle?.author.font;
       const authorFontVariant = CardStyle?.author.variant;
       const iconColor = CardStyle?.icon.color;
  
       // Query body and paragraph elements
       var bodyStyle = document.querySelector('body');
       var p_tag = document.querySelector('p');
       var strong_tag = document.querySelector('strong');
       var h2_tag = document.querySelector('h2');
       var h3_tag = document.querySelector('h3');
       var h1_tag = document.querySelector('h1');

       // Retrieve computed styles or fallback
       var computedStyle = bodyStyle ? window.getComputedStyle(bodyStyle) : p_tag ? window.getComputedStyle(p_tag) : '';

       // Safely get the property values
       var bodyFontSize = computedStyle ? computedStyle.getPropertyValue('font-size') : '';
       var bodyColor = computedStyle && !TagShopEditor ? computedStyle.getPropertyValue('color') : '#2b2b2b';
       var bodyFontFamily = computedStyle ? computedStyle.getPropertyValue('font-family') : '';
       var bodyLineHeight = computedStyle ? computedStyle.getPropertyValue('line-height') : '';

       // Retrieve computed styles or fallback
       var strongComputedStyle = strong_tag ? window.getComputedStyle(strong_tag) : h2_tag ? window.getComputedStyle(h2_tag) : h3_tag ? window.getComputedStyle(h3_tag) : '';

       // NEW CODE START

       var shopifyTitleTag = document.querySelector(".tb_theme_title")
       // Check if the element exists
       if (shopifyTitleTag) {
              // Get the tag name and log it to the console
              var titletagName = shopifyTitleTag.tagName;
              //console.log("titletagName", titletagName); // Output: P
       } else {
              //console.log('Element with class "exanoke" not found.');
       }

       var shopifySubTitleTag = document.querySelector(".tb_theme_sub_title")
       // Check if the element exists
       if (shopifySubTitleTag) {
              // Get the tag name and log it to the console
              var subtitletagName = shopifySubTitleTag.tagName;
              //console.log("subtitletagName", subtitletagName); 
       } else {
              //console.log('Element with class "exanoke" not found.');
       }

       //========================Start======================// 
       var colorValues = [];
       var subcolorValues = [];

       function processH1Elements(titletagName, callback) {
              var h1Elements = document.querySelectorAll(titletagName);
              //console.log("h1Elements",h1Elements);
              h1Elements.forEach(function (element) {
                     if (!element.classList.contains('tb_theme_title')) {
                            callback(element);
                     }
              });
       }

       function processH2Elements(subtitletagName, callback) {
              var h2Elements = document.querySelectorAll(subtitletagName);
              //console.log("h2Elements",h2Elements);
              h2Elements.forEach(function (element_1) {
                     if (!element_1.classList.contains('tb_theme_sub_title')) {
                            callback(element_1);
                     }
              });
       }



       function storeColor(element) {
              var style = window.getComputedStyle(element);
              var title_color = getSuittableColor(style.color);
              var title_size = style.fontSize;
              var title_fontweight = style.fontWeight;
              var title_fontfamily = style.fontFamily;
              var title_textalign = style.textAlign;
              colorValues.push(title_color, title_size, title_fontweight, title_fontfamily, title_textalign);
       }



       // Call the processH1Elements function with the storeColor callback
       processH1Elements(titletagName, storeColor);
       var title_color = colorValues[0] ? colorValues[0] : inheritStyle;
       var title_size = colorValues[1] ? colorValues[1] : inheritStyle;
       var title_fontweight = colorValues[2] ? colorValues[2] : inheritStyle;
       var title_fontfamily = colorValues[3] ? colorValues[3] : inheritStyle;
       var title_textalign = colorValues[4] ? colorValues[4] : inheritStyle;


       function storeColors(element_1) {
              var style = window.getComputedStyle(element_1);
              var subtitle_color = getSuittableColor(style.color);
              var subtitle_size = style.fontSize;
              var subtitle_fontweight = style.fontWeight;
              var subtitle_fontfamily = style.fontFamily;
              var subtitle_textalign = style.textAlign;
              subcolorValues.push(subtitle_color, subtitle_size, subtitle_fontweight, subtitle_fontfamily, subtitle_textalign);
       }
       // Call the processH1Elements function with the storeColor callback
       processH2Elements(subtitletagName, storeColors);
       var subtitle_color = subcolorValues[0] ? subcolorValues[0] : inheritStyle;
       var subtitle_size = subcolorValues[1] ? subcolorValues[1] : inheritStyle;
       var subtitle_fontweight = subcolorValues[2] ? subcolorValues[2] : inheritStyle;
       var subtitle_fontfamily = subcolorValues[3] ? subcolorValues[3] : inheritStyle;
       var subtitle_textalign = subcolorValues[4] ? subcolorValues[4] : inheritStyle;

       //========================End=============================================// 


       // NEW CODE END

       var strongComputedStyling = strong_tag ? window.getComputedStyle(strong_tag) : h1_tag ? window.getComputedStyle(h1_tag) : h2_tag ? window.getComputedStyle(h2_tag) : h3_tag ? window.getComputedStyle(h3_tag) : '';

       // Safely get the property values
       var strongFontSize = strongComputedStyling ? strongComputedStyling.getPropertyValue('font-size') : '';
       var strongColor = strongComputedStyle ? strongComputedStyle.getPropertyValue('color') : '';
       var strongFontFamily = strongComputedStyle ? strongComputedStyle.getPropertyValue('font-family') : '';
       var strongFontWeight = strongComputedStyle ? strongComputedStyle.getPropertyValue('font-weight') : '';
       var strongFontStyle = strongComputedStyle ? strongComputedStyle.getPropertyValue('font-style') : '';

       var a_tag = document.querySelector('a');
       var buttonTag = document.getElementsByTagName('button');
       var btnComputedStyle;
       if (a_tag) {
              btnComputedStyle = window.getComputedStyle(a_tag);
       } else if (buttonTag.length > 0) {
              btnComputedStyle = window.getComputedStyle(buttonTag[0]);
       } else {
              btnComputedStyle = '';
       }

       // Safely get the property values
       var btnColor = btnComputedStyle ? btnComputedStyle.getPropertyValue('color') : '';
       var btnFontFamily = btnComputedStyle ? btnComputedStyle.getPropertyValue('font-family') : '';
       var btnBackground = btnComputedStyle ? btnComputedStyle.getPropertyValue('background-color') : '';
       var btnBorderColor = btnComputedStyle ? btnComputedStyle.getPropertyValue('border-color') : '';

       var color_ = isInherit ? getSuittableColor(bodyColor ? bodyColor : blackColor) : getSuittableColor(CardStyle?.style.color);
       var fontSize_ = isInherit ? bodyFontSize ? bodyFontSize : '14px' : fontSize && String(fontSize).includes('px') ? fontSize : `${fontSize}px`;
       var font_ = isInherit || css_font == '' ? bodyFontFamily ? bodyFontFamily : inheritStyle : css_font ? css_font : inheritStyle;
       var fontWeight_ = splitFontSpec(CardStyle?.style.font).weight ? splitFontSpec(CardStyle?.style.font).weight : 'normal'
       var fontStyle_ = splitFontSpec(CardStyle?.style.font).style ? splitFontSpec(CardStyle?.style.font).style : 'normal'
       var iconColor_ = getSuittableColor(iconColor);
       var lineHeight_ = isInherit ? bodyLineHeight ? bodyLineHeight : '1.5' : '1.5';
       var linkColor_ = getSuittableColor(strongColor ? strongColor : blackColor);
       var cardSize_ = CardStyle?.style.ratio ? `${CardStyle?.style.ratio}%` : '100%';

       const grid_ = ThemeStyle?.column?.desktop
       var row_ = 0
       if (grid_ == 8) row_ = 2
       else if (grid_ == 12) row_ = 3
       else if (grid_ == 18) row_ = 3
       else if (grid_ == 32) row_ = 4
       else row_ = 2

       var authorColor_ = isInherit ? getSuittableColor(strongColor ? strongColor : blackColor) : getSuittableColor(authorColor);
       if (isSocialWall) {
              var authorFont_ = isInherit || css_font == '' ? strongFontFamily ? strongFontFamily : authorFont ? authorFont : 'inherit' : authorFont ? authorFont : 'inherit';
              var authorFontWeight_ = splitFontSpec(authorFontVariant).weight ? splitFontSpec(authorFontVariant).weight : 'normal'
              var authorFontStyle_ = splitFontSpec(authorFontVariant).style ? splitFontSpec(authorFontVariant).style : 'normal'
       } else {
              var authorFont_ = isInherit || css_font == '' ? strongFontFamily ? strongFontFamily : inheritStyle : css_font ? css_font : inheritStyle;
              var authorFontWeight_ = 600
              var authorFontStyle_ = 'normal'
       }

       var baseColorPopUp_ = isInherit ? getTextColor(bodyColor ? bodyColor : blackColor, CardStyle?.style.background) : getTextColor(CardStyle?.style.color, CardStyle?.style.background);

       var fontWeightPopUp_ = splitFontSpec(CardStyle?.style.font).weight ? splitFontSpec(CardStyle?.style.font).weight : 'normal'
       var fontStylePopUp_ = splitFontSpec(CardStyle?.style.font).style ? splitFontSpec(CardStyle?.style.font).style : 'normal'
       var socialIconColorPopUp_ = isInherit ? getTextColor(bodyColor ? bodyColor : blackColor, CardStyle?.style.background) : getTextColor(iconColor, CardStyle?.style.background);
       if (isSocialWall) {
              var fontSizePopUp_ = isEmbed ? isInherit ? 16 : fontSize : 24;
       } else {
              var fontSizePopUp_ = isInherit ? 16 : fontSize;
       }
       var authorColor_PopUp_ = isInherit ? getTextColor(strongColor ? strongColor : blackColor, CardStyle?.style.background) : getTextColor(authorColor, CardStyle?.style.background);


       var CatalogueTitle_font_ = isInherit || ProductSetting?.CatalogueTitle?.font == '' ? strongFontFamily ? strongFontFamily : inheritStyle : CatalogueTitle.font;
       var CatalogueTitle_varient_ = getCssProperty(isInherit, splitFontSpec(CatalogueTitle.variant).style, strongFontStyle, 'font-style', 'normal');
       var CatalogueTitle_weight_ = getCssProperty(isInherit, splitFontSpec(CatalogueTitle.variant).weight, strongFontWeight, 'font-weight', '600');
       var CatalogueTitle_size_ = isInherit ? '16px' : `${CatalogueTitle.size}px`;
       var CatalogueTitle_color_ = isInherit ? getTextColor(strongColor ? strongColor : blackColor, CardStyle?.style.background) : getTextColor(CatalogueTitle.color, CardStyle?.style.background);


       var ProductTitle_font_ = isInherit || ProductTitle.font == '' ? strongFontFamily ? strongFontFamily : inheritStyle : ProductTitle.font ? ProductTitle.font : inheritStyle;
       var ProductTitle_varient_ = getCssProperty(isInherit, splitFontSpec(ProductTitle.variant).style, strongFontStyle, 'font-style', 'normal');
       var ProductTitle_weight_ = getCssProperty(isInherit, splitFontSpec(ProductTitle.variant).weight, strongFontWeight, 'font-weight', '600');
       var ProductTitle_size_ = isInherit ? '14px' : `${ProductTitle.size}px`;
       var ProductTitle_color_ = isInherit ? getTextColor(strongColor ? strongColor : blackColor, CardStyle?.style.background) : getTextColor(ProductTitle.color, CardStyle?.style.background);


       var Price_font_ = isInherit || Price.font == '' ? strongFontFamily ? strongFontFamily : inheritStyle : Price.font ? Price.font : inheritStyle;
       var Price_varient_ = getCssProperty(isInherit, splitFontSpec(Price.variant).style, strongFontStyle, 'font-style', 'normal');
       var Price_weight_ = getCssProperty(isInherit, splitFontSpec(Price.variant).weight, strongFontWeight, 'font-weight', '600');

       var Price_size_ = isInherit ? '14px' : `${Price.size}px`;
       var Price_color_ = isInherit ? getTextColor(strongColor ? strongColor : blackColor, CardStyle?.style.background) : getTextColor(Price.color, CardStyle?.style.background);


       var Button_font_ = isInherit || Button.font == '' ? strongFontFamily ? strongFontFamily : inheritStyle : Button.font ? Button.font : inheritStyle;
       var Button_varient_ = getCssProperty(isInherit, splitFontSpec(Button.variant).style, strongFontStyle, 'font-style', 'normal');
       var Button_weight_ = getCssProperty(isInherit, splitFontSpec(Button.variant).weight, strongFontWeight, 'font-weight', '600');
       var Button_size_ = isInherit ? '14px' : `${Button.size}px`;
       var Button_color_ = Button.color;
       var Button_backgroundColor_ = Button.background;


       var btn_font_ = isInherit ? btnFontFamily ? btnFontFamily : inheritStyle : inheritStyle;
       var btn_varient_ = getCssProperty(isInherit, splitFontSpec(Button.variant).style, strongFontStyle, 'font-style', 'normal');
       var btn_weight_ = getCssProperty(isInherit, splitFontSpec(Button.variant).weight, strongFontWeight, 'font-weight', '600');
       var btn_size_ = isInherit ? '14px' : '14px';
       var btn_color_ = isInherit ? btnColor ? blackColor : '#545454' : '#545454';
       var btn_backgroundColor_ = isInherit ? btnBackground ? btnBackground : whiteColor : whiteColor;

       var btn_borderColor_ = isInherit ? btnBorderColor ? blackColor : '#545454' : '#545454';

       var CTA_font_ = isInherit || CtaData?.font == '' ? strongFontFamily ? strongFontFamily : inheritStyle : CtaData?.font;
       var CTA_varient_ = getCssProperty(isInherit, splitFontSpec(CtaData?.variant).style, strongFontStyle, 'font-style', 'normal');
       var CTA_weight_ = getCssProperty(isInherit, splitFontSpec(CtaData?.variant).weight, strongFontWeight, 'font-weight', '600');
       var CTA_size_ = isInherit ? '14px' : `${CtaData?.size}px`;
       var CTA_color_ = CtaData?.color;
       var CTA_backgroundColor_ = CtaData?.background;

       var Hotspot_color_ = Hotspot.color;
       var Hotspot_backgroundColor_ = Hotspot.background;

       var UgcSetting_font_ = isInherit || SnapUp?.QrCode?.style?.font || SnapUp?.QrCode?.style?.font == '' ? strongFontFamily ? strongFontFamily : inheritStyle : SnapUp?.QrCode?.style?.font;

       var UgcSetting_varient_ = getCssProperty(isInherit, splitFontSpec(SnapUp?.QrCode?.style?.variant).style, strongFontStyle, 'font-style', 'normal');
       var UgcSetting_weight_ = getCssProperty(isInherit, splitFontSpec(SnapUp?.QrCode?.style?.variant).weight, strongFontWeight, 'font-weight', '600');
       var UgcSetting_size_ = isInherit ? '14px' : `${SnapUp?.QrCode?.style?.size}px`;
       var UgcSetting_color_ = SnapUp?.QrCode?.style?.color;
       var UgcSetting_backgroundColor_ =SnapUp?.QrCode?.style?.background;

       // var UgcBtn_font_ = UgcSettings?.onsite_css_font_btn;
       // var UgcBtn_varient_ = getCssProperty(isInherit, splitFontSpec(UgcSettings?.variant).style, strongFontStyle, 'font-style', 'normal');
       // var UgcBtn_weight_ = getCssProperty(isInherit, splitFontSpec(UgcSettings?.variant).weight, strongFontWeight, 'font-weight', '600');
       // var UgcBtn_size_ = `${UgcSettings?.onsite_txt_font_size}px`;
       // var UgcBtn_color_ = UgcSettings?.onsite_btn_txt_color;
       // var UgcBtn_bg_ = UgcSettings?.onsite_btn_color;

       var Ti_title_size = `${CustomTicker?.title_font_size}px`;
       var Ti_title_font = CustomTicker?.subtitle_css_font;
       var Ti_title_color = CustomTicker?.title_font_color;
       var Ti_title_background = CustomTicker?.title_backgroud_color;
       var Ti_title_weight = splitFontSpec(CustomTicker?.title_font_varient).weight;


       var Ti_sTitle_size = `${CustomTicker?.subTitle_font_size}px`;
       var Ti_sTitle_font = CustomTicker?.subtitle_css_font;
       var Ti_sTitle_color = CustomTicker?.subTitle_font_color;
       var Ti_sTitle_background = CustomTicker?.subTitle_backgroud_color;
       var Ti_sTitle_weight = splitFontSpec(CustomTicker?.subtitle_font_varient).weight;

       var Ticker_H = CustomTicker?.status == 1 ? parseInt(CustomTicker?.ticker_height) : 0
       var Banner_H = CustomBanner?.status == 1 ? parseInt(CustomBanner?.banner_height) : 0



       var re_t_St = isInherit ? strongFontFamily ? strongFontFamily : inheritStyle : Reviews?.title?.style?.font;
       var re_t_St_varient_ = getCssProperty(isInherit, splitFontSpec(Reviews?.title?.style?.variant).style, strongFontStyle, 'font-style', 'normal');
       var re_t_St_weight_ = getCssProperty(isInherit, splitFontSpec(Reviews?.title?.style?.variant).weight, strongFontWeight, 'font-weight', '600');
       var re_t_St_size_ = isInherit ? '14px' : `${Reviews?.title?.style?.size}px`;
       var re_t_St_color_ = Reviews?.title?.style?.color;


       var re_btnSt_font = isInherit ? strongFontFamily ? strongFontFamily : inheritStyle : Reviews?.button?.style?.font;
       var re_btnSt_varient_ = getCssProperty(isInherit, splitFontSpec(Reviews?.button?.style?.variant).style, strongFontStyle, 'font-style', 'normal');
       var re_btnSt_weight_ = getCssProperty(isInherit, splitFontSpec(Reviews?.button?.style?.variant).weight, strongFontWeight, 'font-weight', '600');
       var re_btnSt_size_ = isInherit ? '14px' : `${Reviews?.button?.style?.size}px`;
       var re_btnSt_color_ = Reviews?.button?.style?.color;
       var re_btnSt_background_ = Reviews?.button?.style?.background;

       var re_star_color_ = Reviews?.star?.color;

       // var headerStar_color_ = UgcSettings?.ratingColor ? UgcSettings?.ratingColor : '#F8B90C';
       const cardStyle = {
              color: color_,
              fontSize: fontSize_,
              font: font_,
              fontWeight: fontWeight_,
              fontStyle: fontStyle_,
              iconColor: iconColor_,
              lineHeight: lineHeight_,
              linkColor: linkColor_,
              background: CardStyle?.style.background,
              authorColor: authorColor_,
              cardPadding: ThemeRule?.padding ? `${ThemeRule.padding}px` : '0px',
              authorFont: authorFont_,
              authorFontWeight: authorFontWeight_,
              authorFontStyle: authorFontStyle_,
              radius: getRoundedTheme() ? `50%` : `${wall?.CardStyle?.style.radius}px`,
              iconPosition: `${(wall?.CardStyle?.style.radius ? wall?.CardStyle?.style.radius / 3.5 : 0 / 3.5) + 12}px`,
              cardSize: cardSize_,
              rows: row_
       }
       const ctaBtn = {
              font: CTA_font_,
              variant: CTA_varient_,
              weight: CTA_weight_,
              size: CTA_size_,
              color: CTA_color_,
              background: CTA_backgroundColor_,
              padding: '8px 10px',
              radius: '3px',
              minHeight: '40px',
              transition: 'all .3s ease-out 0s',
              colorHover: CTA_color_,
              backgroundHover: CTA_backgroundColor_,
       }
       const button = {
              font: btn_font_,
              variant: btn_varient_,
              weight: btn_weight_,
              size: btn_size_,
              color: btn_color_,
              borderColor: btn_borderColor_,
              background: btn_backgroundColor_,
              padding: '8px 10px',
              radius: '3px',
              minHeight: '40px',
              transition: 'all .3s ease-out 0s',
              colorHover: btn_color_,
              backgroundHover: btn_backgroundColor_,
       }
       const popUpProperties = {
              background: CardStyle?.style.background,
              font: font_,
              fontSize: `${fontSizePopUp_}px`,
              color: baseColorPopUp_,
              iconColor: socialIconColorPopUp_,
              fontWeight: fontWeightPopUp_,
              fontStyle: fontStylePopUp_,
              authorFont: authorFont_,
              authorFontSize: '14px',
              authorColor: authorColor_PopUp_,
              authorfontWeight: '600',
       }
       const ProductSettings = {
              CatalogueTitle: {
                     font: CatalogueTitle_font_,
                     variant: CatalogueTitle_varient_,
                     weight: CatalogueTitle_weight_,
                     size: CatalogueTitle_size_,
                     color: CatalogueTitle_color_,
              },
              ProductTitle: {
                     font: ProductTitle_font_,
                     variant: ProductTitle_varient_,
                     weight: ProductTitle_weight_,
                     size: ProductTitle_size_,
                     color: ProductTitle_color_,
              },
              Price: {
                     font: Price_font_,
                     variant: Price_varient_,
                     weight: Price_weight_,
                     size: Price_size_,
                     color: Price_color_,
              },
              Button: {
                     font: Button_font_,
                     variant: Button_varient_,
                     weight: Button_weight_,
                     size: Button_size_,
                     color: Button_color_,
                     background: Button_backgroundColor_,
              },
              Hotspot: {
                     color: Hotspot_color_,
                     background: Hotspot_backgroundColor_,
                     status: 1
              }
       }

       const UgcSettingData = {
              font: UgcSetting_font_,
              variant: UgcSetting_varient_,
              weight: UgcSetting_weight_,
              size: UgcSetting_size_,
              color: UgcSetting_color_,
              background: UgcSetting_backgroundColor_
       }
       const reviewTitle = {
              font: re_t_St,
              variant: re_t_St_varient_,
              weight: re_t_St_weight_,
              size: re_t_St_size_,
              color: re_t_St_color_
       }
       const reviewStar = {
              color: re_star_color_
       }
       const reviewBtn = {
              font: re_btnSt_font,
              variant: re_btnSt_varient_,
              weight: re_btnSt_weight_,
              size: re_btnSt_size_,
              color: re_btnSt_color_,
              background: re_btnSt_background_,
       }


       const heading = {
              title: {
                     size: title_size,
                     font: title_fontfamily,
                     fontWeight: title_fontweight,
                     color: title_color,
                     textalign: title_textalign,
              }

       }
       const subheading = {
              subtitle: {
                     size: subtitle_size,
                     font: subtitle_fontfamily,
                     fontWeight: subtitle_fontweight,
                     color: subtitle_color,
                     textalign: subtitle_textalign,
              }

       }
       const ticker = {
              heading: {
                     size: Ti_title_size,
                     font: Ti_title_font,
                     fontWeight: Ti_title_weight,
                     color: Ti_title_color,
                     background: Ti_title_background
              },
              desc: {
                     size: Ti_sTitle_size,
                     font: Ti_sTitle_font,
                     fontWeight: Ti_sTitle_weight,
                     color: Ti_sTitle_color,
                     background: Ti_sTitle_background
              }

       }
       const height = {
              banner: `${Banner_H}px`,
              ticker: `${Ticker_H}px`,
       }

       return { cardStyle, ctaBtn, button, popUpProperties, ProductSettings, SnapUp, heading, subheading, ticker, height, reviewTitle, reviewBtn, reviewStar }
}
